import Vue from 'vue';
//配置路由
import VueRouter from 'vue-router'
Vue.use(VueRouter);
    // 解决多次点击左侧菜单报错问题
    const VueRouterPush = VueRouter.prototype.push
    VueRouter.prototype.push = function push (to) {
    return VueRouterPush.call(this, to).catch(err => err)
    }
//1.创建组件
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
import beifen from '@/views/modules/databaseBackup/beifen'
import huanyuan from '@/views/modules/databaseBackup/huanyuan'

     import users from '@/views/modules/users/list'
    import dictionary from '@/views/modules/dictionary/list'
    import gonggao from '@/views/modules/gonggao/list'
    import menu from '@/views/modules/menu/list'
    import qiye from '@/views/modules/qiye/list'
    import shijianrenshu from '@/views/modules/shijianrenshu/list'
    import shijianrenshuChat from '@/views/modules/shijianrenshuChat/list'
    import shijianrenshuCollection from '@/views/modules/shijianrenshuCollection/list'
    import shijianrenshuLiuyan from '@/views/modules/shijianrenshuLiuyan/list'
    import shijianrenshuOrder from '@/views/modules/shijianrenshuOrder/list'
    import yonghu from '@/views/modules/yonghu/list'
    import config from '@/views/modules/config/list'
    import dictionaryGonggao from '@/views/modules/dictionaryGonggao/list'
    import dictionaryHangye from '@/views/modules/dictionaryHangye/list'
    import dictionaryRenwuzhuangtai from '@/views/modules/dictionaryRenwuzhuangtai/list'
    import dictionarySex from '@/views/modules/dictionarySex/list'
    import dictionaryShangxia from '@/views/modules/dictionaryShangxia/list'
    import dictionaryShijianrenshu from '@/views/modules/dictionaryShijianrenshu/list'
    import dictionaryShijianrenshuChat from '@/views/modules/dictionaryShijianrenshuChat/list'
    import dictionaryShijianrenshuCollection from '@/views/modules/dictionaryShijianrenshuCollection/list'
    import dictionaryShijianrenshuOrder from '@/views/modules/dictionaryShijianrenshuOrder/list'
    import dictionaryShijianrenshuOrderYesno from '@/views/modules/dictionaryShijianrenshuOrderYesno/list'
    import dictionaryZhuangtai from '@/views/modules/dictionaryZhuangtai/list'





//2.配置路由   注意：名字
const routes = [{
    path: '/index',
    name: '首页',
    component: Index,
    children: [{
      // 这里不设置值，是把main作为默认页面
      path: '/',
      name: '首页',
      component: Home,
      meta: {icon:'', title:'center'}
    }, {
      path: '/updatePassword',
      name: '修改密码',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '支付',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '个人信息',
      component: center,
      meta: {icon:'', title:'center'}
    }, {
        path: '/huanyuan',
        name: '数据还原',
        component: huanyuan
    }, {
        path: '/beifen',
        name: '数据备份',
        component: beifen
    }, {
        path: '/users',
        name: '管理信息',
        component: users
    }
    ,{
        path: '/dictionaryGonggao',
        name: '公告类型',
        component: dictionaryGonggao
    }
    ,{
        path: '/dictionaryHangye',
        name: '行业',
        component: dictionaryHangye
    }
    ,{
        path: '/dictionaryRenwuzhuangtai',
        name: '任务状态',
        component: dictionaryRenwuzhuangtai
    }
    ,{
        path: '/dictionarySex',
        name: '性别类型',
        component: dictionarySex
    }
    ,{
        path: '/dictionaryShangxia',
        name: '上下架',
        component: dictionaryShangxia
    }
    ,{
        path: '/dictionaryShijianrenshu',
        name: '任务类型',
        component: dictionaryShijianrenshu
    }
    ,{
        path: '/dictionaryShijianrenshuChat',
        name: '数据类型',
        component: dictionaryShijianrenshuChat
    }
    ,{
        path: '/dictionaryShijianrenshuCollection',
        name: '收藏表类型',
        component: dictionaryShijianrenshuCollection
    }
    ,{
        path: '/dictionaryShijianrenshuOrder',
        name: '状态类型',
        component: dictionaryShijianrenshuOrder
    }
    ,{
        path: '/dictionaryShijianrenshuOrderYesno',
        name: '审核状态',
        component: dictionaryShijianrenshuOrderYesno
    }
    ,{
        path: '/dictionaryZhuangtai',
        name: '状态',
        component: dictionaryZhuangtai
    }
    ,{
        path: '/config',
        name: '轮播图',
        component: config
    }


    ,{
        path: '/dictionary',
        name: '字典',
        component: dictionary
      }
    ,{
        path: '/gonggao',
        name: '公告',
        component: gonggao
      }
    ,{
        path: '/menu',
        name: '菜单',
        component: menu
      }
    ,{
        path: '/qiye',
        name: '企业',
        component: qiye
      }
    ,{
        path: '/shijianrenshu',
        name: '任务',
        component: shijianrenshu
      }
    ,{
        path: '/shijianrenshuChat',
        name: '任务交流',
        component: shijianrenshuChat
      }
    ,{
        path: '/shijianrenshuCollection',
        name: '任务收藏',
        component: shijianrenshuCollection
      }
    ,{
        path: '/shijianrenshuLiuyan',
        name: '任务留言',
        component: shijianrenshuLiuyan
      }
    ,{
        path: '/shijianrenshuOrder',
        name: '任务订单',
        component: shijianrenshuOrder
      }
    ,{
        path: '/yonghu',
        name: '用户',
        component: yonghu
      }


    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '/',
    name: '首页',
    redirect: '/index'
  }, /*默认跳转路由*/
  {
    path: '*',
    component: NotFound
  }
]
//3.实例化VueRouter  注意：名字
const router = new VueRouter({
  mode: 'hash',
  /*hash模式改为history*/
  routes // （缩写）相当于 routes: routes
})

export default router;
